const allStrings = [() => [{ "nodeName": "div", "children": [{ "nodeName": "small", "children": [{ "nodeName": "text", "textContent": "(" }, { "nodeName": "text", "value": 0 }, { "nodeName": "text", "textContent": " render count " }, { "nodeName": "span", "attributes": [["id", { "tagJsVar": 1 }]], "children": [{ "nodeName": "text", "value": 2 }] }, { "nodeName": "text", "textContent": ")" }] }] }]];
