import { html, tag } from "taggedjs";
export const arrayFunTag = tag((item, deleteItem) => html.dom(allStrings[0], item, () => deleteItem(item)));
// @ts-ignore
const allStrings = [() => [{ "nodeName": "div", "attributes": [["style", "border:1px solid black;"]], "children": [{ "nodeName": "text", "value": 0 }, { "nodeName": "button", "attributes": [["type", "button"], ["onclick", { "tagJsVar": 1 }]], "children": [{ "nodeName": "text", "textContent": "delete" }] }] }]];
